<script setup>
 import {ref,onMounted,reactive } from 'vue';
 import {  getAuth, onAuthStateChanged} from "firebase/auth";
 import { query, getFirestore ,collection,addDoc,doc,getDocs,setDoc,update } from "firebase/firestore";
 import {  getDownloadURL, getStorage ,ref as storageRef,uploadBytes, uploadBytesResumable} from 'firebase/storage';
 const userData=reactive({user:"",userUid:""});
 const db=getFirestore();
 const storage = getStorage();
    //創建圖片

    const selectedFile = ref();//用戶選擇的圖片檔案
    const fileInput=ref(null);//檔案輸入框的參考
    const downloadUrl=ref();//上傳圖片後的連結
    const uploadProgress=ref(null);//上傳的進度

    const handleFileSelect = event => {
    selectedFile.value = event.target.files[0];
    console.log(selectedFile.value)
    };
    // const selectMultFiles=reactive({data:[]})
    // const handleMult=e=>{
    //     selectMultFiles.data=Array.from(e.target.files);
    //     console.log(selectMultFiles)
    // }
    const selectMultFiles=ref()
    const handleMult=e=>{
        selectMultFiles.value=Array.from(e.target.files);
        console.log(selectMultFiles.value)
    }
    const uploadMult=()=>{
        if (!selectMultFiles.value) {
            return;
        }
        selectMultFiles.value.forEach((items)=>{
         const storageName = storageRef(storage, `images/${items.name}`);
        // uploadBytes(storageName, selectMultFiles.value).then(snapshot => {
        //     console.log('Uploaded a blob or file!'); //上傳成功
        // });
            const uploadTask=uploadBytesResumable(storageName, items);
            uploadTask.on('state_changed',(snapshot)=>{
                const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                console.log('Upload is ' + progress + '% done');
                console.log('Upload state ', snapshot.state);
                uploadProgress.value = progress;
            },
            (error)=>{
                console.log('error'+ error)
            },
            ()=>{
                console.log("tyw")
                
                getDownloadURL(uploadTask.snapshot.ref).then((downUrl)=>{
                    console.log("hwiue")
                    console.log(downUrl);
                    downloadUrl.value=downUrl;
                    if(data1.img.length==0){
                        let pathDetail={
                            sortIdx:0,
                            fullpath:downUrl
                        }
                        data1.img.push(pathDetail);
                    }else{
                        let lastSordIdx=data1.img[data1.img.length - 1 ].sortIdx;
                        let pathDetail={
                            sortIdx:lastSordIdx + 1,
                            fullpath:downUrl
                        };
                        data1.img.push(pathDetail);
                    }
                    console.log(downloadUrl.value);
                })
                // console.log('File available at', downloadUrl.value);
            })
        })
        
    }
    //單張上傳圖片
    const upload = () => {
        if (!selectedFile.value) {
            return;
        }
        const storageName = storageRef(storage, `images/${selectedFile.value.name}`);
        // uploadBytes(storageName, selectedFile.value).then(snapshot => {
        //     console.log('Uploaded a blob or file!'); //上傳成功
        // });
        const uploadTask=uploadBytesResumable(storageName, selectedFile.value);
        uploadTask.on('state_changed',(snapshot)=>{
            console.log(snapshot);
            const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
            console.log('Upload is ' + progress + '% done');
            console.log('Upload state ', snapshot.state);
            uploadProgress.value = progress;
        },
        (error)=>{
            console.log('error'+ error)
        },
        ()=>{
            console.log("tyw")
            
            getDownloadURL(uploadTask.snapshot.ref).then((downUrl)=>{
                console.log("hwiue")
                console.log(downUrl);
                downloadUrl.value=downUrl;
                console.log(downloadUrl.value);
            })
            // console.log('File available at', downloadUrl.value);
        })
    };


const data1= reactive({
    name:"",//商品名稱
    isSize:false,//是否有尺寸可選擇
    isqty:false,//是否現貨
    img:[],
    pdSize:[
        {
            productName:"",//規格名稱
            price:"",//商品金額
            sortIndex: 0,//商品排序
            qty:0,//數量
        }
    ],
    tag:[""],//標籤
    isPreOrder:true,


});

const addtag=(idx)=>{
    const str="";
    data1.tag.push(str)
}
const deleteTag=(idx)=>{
    data1.tag.splice(idx, 1);
       
}
    const addOptions=(sortIdx)=>{
        console.log("23")
        const newsOption={
            productName:"",
            price:"",
            sortIndex: sortIdx,
            qty:0
        }
        data1.pdSize.push(newsOption)
    }
    const deleteOptions=(idx)=>{
        data1.pdSize.splice(idx, 1);
        sortOptions();
        
    }
    const sortOptions=()=> {
            for (let i = 0; i < data1.pdSize.length; i++) {
                data1.pdSize[i].sortIndex = i;
            }
        };


        const goodsCreat=async(data1,userId)=>{
    console.log("1")
    //商品內容
    // const shoppingListData = [
    // { id: 1, name: 'Product A', quantity: 2 },
    // { id: 2, name: 'Product B', quantity: 1 },
    // ];

    //let username=userData.userUid;
    
     const docRef=addDoc(collection(db, 'products'),data1);
     //setDoc(docRef,{ goods: data1 });


}

    const pdSize=reactive({data:[]});
    onMounted(()=>{
        const auth = getAuth();
        onAuthStateChanged(auth, (user) => {
                if (user) {
                    // User is signed in, see docs for a list of available properties
                    // https://firebase.google.com/docs/reference/js/auth.user
                    userData.user=user.email;
                    userData.userUid=user.uid
                    // ...
                } else {
                    // User is signed out
                    // ...
                }
                });
                
                fetchData();
                
    });
///新增商品資料表

const data2=reactive({data:[]})

const fetchData = async () => {
    const q = query(collection(db, "products"));
    const querySnapshot = await getDocs(q);
    querySnapshot.forEach((doc) => {
    // doc.data() is never undefined for query doc snapshots
    console.log(doc.id, " => ", doc.data());
    data2.data.push(doc.data())
    console.log(data2.data)
    });
}

</script>
<template>
      <div> 帳號:{{userData.user}}<br />
            UID:{{userData.userUid}}</div>
    <div class="container">{{ data1 }}</div>
    <div class="container">
        <h1>商品上傳</h1>
        <label for="">商品名稱</label>
        <input class="form-control" type="text" v-model="data1.name">
        <label for="">圖片上傳</label>
        <div>
            <input ref="fileInput" type="file" accept="image/*" @change="handleFileSelect" />
            <button @click="upload">upload</button>
        </div>
        <label for="">多張圖片上傳</label>
        <div>
            <input ref="fileInput" type="file" accept="image/*" multiple="multiple" @change="handleMult" />
            <button @click="uploadMult">upload</button>
        </div>
        <div>
      <div >
        <div>{{ uploadProgress }}%
        </div>
      </div>
      <p v-if="data1.img.length!==0">
        <img v-for="(img,idx) in data1.img" :key="idx" :src="img.fullpath" />
      </p>
      <p v-else >圖片顯示於此</p>
    </div>
        <!-- <label for="">商品編號</label>
        <input class="form-control" type="text"> -->
        <label for="">是否現貨</label>
        <input class="" type="checkbox" v-model="data1.isqty">
        <label for="">商品是否有款式</label>
        
        <input type="checkbox" v-model="data1.isSize">
        <br>
        <label for="">是否為預購品</label>
        
        <input type="checkbox" v-model="data1.isPreOrder">
        <br>
            <h2>款式名稱跟價格</h2>

        <div>
            <button class="btn btn-danger" @click="addOptions(data1.pdSize.length)">新增</button>
        </div>
        <div class="row" v-for="(item, idx) in data1.pdSize" :key="item.name">
            <div class="col-3">
                <label for="">款式名稱</label>
                <input class="form-control" type="text" v-model="item.productName">
            </div>
            <div class="col-3">
                <label for="">款式數量</label>
                <input class="form-control" type="text" v-model="item.price">
            </div>
            <div class="col-3">
                <label for="">款式價格</label>
                <input class="form-control" type="text" v-model="item.qty">
            </div>
            <div class="col-2">
                <label for="">刪除</label><br>
                <button class="btn btn-primary" @click="deleteOptions(idx)">刪除</button>
            </div>
        </div>
        標籤
        <div class="row" >
            <div class="col-2" v-for="(item,index) in data1.tag" :key="index " ><input class="form-control d-inline-block" style="width: 100px;" type="text"  v-model="data1.tag[index]" >
                <button class="btn btn-primary" @click="deleteTag(index)">刪除</button>
            </div>
            
        </div>
        <div>
            <button class="btn btn-danger" @click="addtag()">新增標籤</button>
        </div>

        <button class="btn btn-success" @click="goodsCreat(data1)">上傳</button>
    </div>
    
    <div class="container">
        新增商品資料表
        {{ data2.data }}

    </div>

    <hr>

    

</template>
